.App {
  text-align: center;
}

.row {
  display:flex;
  justify-content: center;
  margin: 15px 0;
}

.card {
  border: 1px solid black;
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.3);
  margin-bottom: 15px;
  justify-content: center;
  background-color: darkseagreen;
}
